div.VerifyEmailNotice{
    margin: 1.25rem 0;
}
div.VerifyEmailNotice>p.Title{
    font-size: var(--font-size-extra);
    color: var(--color-red-light);
    margin-bottom: 1.25rem;
}
div.VerifyEmailNotice>p.Description{
    font-size: var(--font-size-normal);
    text-transform: uppercase;
    margin-bottom: 1.25rem;
}
div.VerifyEmailNotice>p.Description>span{
    text-transform: none;
    font-weight: 600;
    color: var(--color-gold-light);
}
div.VerifyEmailNotice>button{
    padding: 0.25rem 1.5rem;
    min-width: 10rem;
}
#Dashboard{
    width: 100%;
    height: 100%;
    min-height: 500px;                                  /* Hard-coded for now */
}

div.MessageScreen{
    height: 100vh;
    width: 100vw;
    max-width: calc(576px + calc((100vw - 576px) * 0.2));
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 10;
    padding: 1rem 1.5rem;
    -webkit-animation: FadeIn 1.5s;
            animation: FadeIn 1.5s;
}

div.MessageScreen.error{
    background: -webkit-linear-gradient(rgba(255, 1, 0, 0.8), rgba(140, 40, 10, 0.8));
    background: linear-gradient(rgba(255, 1, 0, 0.8), rgba(140, 40, 10, 0.8));
}
div.MessageScreen.success{
    background: -webkit-linear-gradient(rgba(0, 255, 0, 0.8), rgba(22, 78, 6, 0.8));
    background: linear-gradient(rgba(0, 255, 0, 0.8), rgba(22, 78, 6, 0.8));
}
div.MessageScreen.info{
    background: -webkit-linear-gradient(rgba(16, 243, 232, 0.9), rgba(20, 99, 154, 0.9));
    background: linear-gradient(rgba(16, 243, 232, 0.9), rgba(20, 99, 154, 0.9));
}
div.MessageScreen.search{
    background: -webkit-linear-gradient(rgba(235, 235, 235, 0.9), rgba(200, 200, 200, 0.9));
    background: linear-gradient(rgba(235, 235, 235, 0.9), rgba(200, 200, 200, 0.9));
    justify-content: space-around;
}
div.MessageScreen>div.Content{
    width: 100%;
    color: var(--color-white-light);
    font-weight: 500;
    text-transform: capitalize;
    font-size: var(--font-size-large);
    letter-spacing: 0.1rem;
    white-space: pre-wrap;
    text-align: center;
}
div.MessageScreen>button{
    padding: 0.25rem 1.5rem;
    min-width: 10rem;
    margin-top: 1.5rem;
    font-weight: 600;
}
div.CustomLoader{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    min-width: 10rem;
    min-height: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
div.CustomLoader>span{
    line-height: 1 !important;
}


#Menu{
    display: inline-flex;
    width: 18rem;
    position: fixed;
    bottom: 0.75rem;
    justify-content: space-between;
    align-items: center;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    color: var(--color-black-light);
    z-index: 5;
}

#Menu-Left{
    z-index: 1;
    border-radius: 0.25rem;
    height: 4rem;
    width: 8rem;
    background-image: -webkit-radial-gradient(100% 0, circle, transparent 35%, var(--color-grey-normal) 35%, var(--color-white-dark)  70%, var(--color-grey-normal)  100%);
    background-image: -moz-radial-gradient(100% 0, circle, transparent 35%, var(--color-grey-normal)  35%, var(--color-white-dark) 70%, var(--color-grey-normal)  100%);
}
#Menu-Left>*{
    width: 100%;
    height: 100%;
    padding: 0.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}
#Menu-Left>*>i{
    -webkit-transform: translateX(calc(8rem * 0.65 * 0.5 - 50%));
            transform: translateX(calc(8rem * 0.65 * 0.5 - 50%));
}
#Menu-Left>*>span{
    -webkit-transform: translateX(calc(8rem * 0.65 * 0.5 - 50%));
            transform: translateX(calc(8rem * 0.65 * 0.5 - 50%));
}
#Menu-Right{
    z-index: 1;
    border-radius: 0.25rem;
    height: 4rem;
    width: 8rem;
    background-image: -webkit-radial-gradient(0 0, circle, transparent 35%, var(--color-grey-normal) 35%, var(--color-white-dark)  70%, var(--color-grey-normal) 100%);
    background-image: -moz-radial-gradient(0 0, circle, transparent 35%, var(--color-grey-normal) 35%, var(--color-white-dark)  70%, var(--color-grey-normal)  100%);
    position: relative;
}
#Menu-Right>*:not(i){
    width: 100%;
    height: 100%;
    padding: 0.5rem 0rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
}
#Menu-Right>*>i{
    -webkit-transform: translateX(calc(-1 * 8rem * 0.65 * 0.5 + 50%));
            transform: translateX(calc(-1 * 8rem * 0.65 * 0.5 + 50%));
}
#Menu-Right>*>span{
    -webkit-transform: translateX(calc(-1 * 8rem * 0.65 * 0.5 + 50%));
            transform: translateX(calc(-1 * 8rem * 0.65 * 0.5 + 50%));
}
#Menu-Center{
    z-index: 2;
    background-image: -webkit-radial-gradient(50% 50%, circle, var(--color-white-dark) 25%, var(--color-grey-normal) 100%);
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    position: absolute;
    -webkit-transform: translateY(-2rem) translateX(6.5rem);
            transform: translateY(-2rem) translateX(6.5rem);
}
#Menu-Center>*{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#Menu-Center img{
    height: 2rem;
}
#Menu-Left>*.Active,
#Menu-Center>*.Active,
#Menu-Right>*.Active{
    color: var(--color-gold-dark);
    font-weight: 600;
}
#Menu-Left>*>*,
#Menu-Center>*>*,
#Menu-Right>*>*{
    -webkit-transition: all 1s;
    transition: all 1s;
}
#Menu-Right>i.KYC{
    position: absolute;
    top: 0rem;
    right: 0.25rem;
    color: var(--color-red-light);
    -webkit-animation: TextShinning 2s ease-in-out infinite alternate;
            animation: TextShinning 2s ease-in-out infinite alternate;
}
#Menu.Shopping>#Menu-Left,
#Menu.Shopping>#Menu-Right{
    opacity: 0.15;
}
#Menu.Shopping>#Menu-Left>*:hover,
#Menu.Shopping>#Menu-Right>*:hover{
    cursor: not-allowed;
}
#Menu.Shopping>#Menu-Center{
    background: -webkit-linear-gradient(var(--color-black-light), var(--color-black-dark) 50%, var(--color-black-light));
    background: linear-gradient(var(--color-black-light), var(--color-black-dark) 50%, var(--color-black-light));
    color: var(--color-gold-light);
    -webkit-animation: ShopHeartBeat 0.25s ease infinite alternate;
            animation: ShopHeartBeat 0.25s ease infinite alternate;
    -webkit-transform: scale(0.8) translateY(-2rem) translateX(8.25rem);
            transform: scale(0.8) translateY(-2rem) translateX(8.25rem);
}
#Menu.Shopping>#Menu-Center:hover{
    cursor: pointer;
}
@-webkit-keyframes ShopHeartBeat{
    0%{-webkit-transform: scale(0.8) translateY(-2rem) translateX(8.25rem);transform: scale(0.8) translateY(-2rem) translateX(8.25rem);}
    100%{-webkit-transform: scale(1) translateY(-2rem) translateX(6.5rem);transform: scale(1) translateY(-2rem) translateX(6.5rem);}
}
@keyframes ShopHeartBeat{
    0%{-webkit-transform: scale(0.8) translateY(-2rem) translateX(8.25rem);transform: scale(0.8) translateY(-2rem) translateX(8.25rem);}
    100%{-webkit-transform: scale(1) translateY(-2rem) translateX(6.5rem);transform: scale(1) translateY(-2rem) translateX(6.5rem);}
}
